/* viewport */

/* vars */
body { padding-top: 50px; background: #ffffff;}
:focus { outline: none; }
a:hover { text-decoration: none; box-shadow: 0 0.1px 0 #000; }
pre { border: none; white-space: pre-wrap; }
iframe, object, embed { max-width: 100%; }

/* response videos */
.video { position: relative; padding-bottom: 56.25%; padding-top: 30px; height: 0; overflow: hidden; }
.video iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

/* add loading state to images */
[async-src]{ background: url(/theme/default/img/wave.svg) no-repeat 0 0; background-size: 44px 44px; min-height: 44px; min-width: 44px;}

.container{ width: 90%; max-width: 800px; padding: 0 10px; margin: 0 auto; }
.horizon { position: absolute; left: 50%; -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%); transform: translateX(-50%); }
.center { position: absolute; left: 50%; top: 50%; -webkit-transform: translate(-50%, -50%); -ms-transform: translate(-50%, -50%); transform: translate(-50%, -50%); text-align: center;}
.up { top: 40%; }

/* navigation */
.nav { position: fixed; height: 44px; line-height: 44px; width: 100%; top:0; left: 0; border-bottom: 1px solid #eee; background: #fff; padding: 0 20px; z-index: 1; font-size: 14px;}
.nav-item { padding:0 10px; display: inline-block; color: #000; -webkit-transform: translateZ(0); transform: translateZ(0); }
.nav strong { color: #16a085; }

.search{ position: absolute; right: 0;}
.search-input{ border: none; display: block; width: 300px; height: 44px; background:none; padding:0 20px;
}
.search-input:focus { background: #f6f6f6;}
.search-complete { position: absolute; top: 44px; left: 0; width: 100%; line-height: 1.65; background: #fff;
}
.search-complete li { padding: 10px 20px; border: 1px solid #f6f6f6; border-width: 1px 5px;}
.search-complete li:last-of-type { border-bottom-width: 5px;}

@media (max-width: 640px) {
  .search { display: none; }
}


.logo{ padding: 56px 0px 30px; font-size: 40px; font-weight: 900; font-family: serif; margin-left: -7px;margin-top: 0;}

#comment { position: relative; padding: 20px 0; }
#comment::before {
  content: '————';
  display: block;
  margin: 30px;
  color: #eee;
  text-align: center;
}
#comment-loader { bottom: 20px; cursor: pointer; }

#footer { clear: both; padding: 60px 0 30px; text-align: center; font-weight: 100;}



